import React, { useEffect, useState } from 'react';
import "./css/style.css";
const Weather=()=>{
    const [city,setCity]=useState(null);
    const [search,setSearch]=useState("Mumbai");
    useEffect( ()=>{
        const fetchApi= async()=>{
            const url=`https://api.openweathermap.org/data/2.5/weather?q=${search}&units=metric&appid=4aac1b0116d4b0bb0b6a07861273626b`
            const response=await fetch(url);
            //console.log(response);
            const resJson= await response.json();
            //console.log(resJson);
            setCity(resJson.main)
        }
        fetchApi();
    },[search])
    return(
        <>
        <div className='box'>
            <div className='inputData'>
               <input
               type="Search"
               className="inputField" 
               onChange={(event)=>{ setSearch(event.target.value)}}
               />
            </div>
        {!city?(
            <p className="errorMsg">data not found</p>
        ):(
            <div>
            <div className="info">
          <h2 className="location">
          <i className="fas fa-street-view"></i>{search}
          </h2> 
          <h1 className="temp">
          {city.temp}°C
          </h1>
          <h3 className="temmin_max">Min:{city.temp_min}°C | Max:{city.temp_max}°C</h3>
        </div>
        <div className="wave -one"></div>
        <div className="wave -two"></div>
        <div className="wave -three"></div>
        </div>
        )}
        
        </div>
        </>
    );
};

export default Weather; 